import { Component, OnInit, Type, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Subject, Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { ClientesService } from './../shared/clientes.service';
import { DetalleClienteComponent } from './detalle/detalle-cliente.component';

declare var $: any;

@Component({
  selector: 'clientes',
  templateUrl: './clientes.component.html',
  styles: []
})
export class ClientesComponent implements OnInit {
  itemsObs: Subject<any> = new Subject();
  dtTrigger: Subject<any> = new Subject();
  dcomp: Type<any> = DetalleClienteComponent;

  options = {
    dom: 'Bfrtip',
    order: [[4, 'desc']],
    colReorder: true
  };

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private clientsServices: ClientesService
  ) { }

  ngOnInit() {
    this.clientsServices.getClientes()
      .subscribe((data: any) => {
        this.itemsObs.next(data.result);
      })
  }

  Editar(data: any): void {
    this.router.navigate(['editar', data._id], {relativeTo: this.route});
  }

}



